.container {
  width: 100%;
  display: flex;
}
.profile_image {
  width: 350px;
  padding: 30px;
  background-color: var(--s-dark);
  flex-shrink: 0;
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
}
.image {
  width: 100%;
  height: 290px;
  border-radius: 50%;
  overflow: hidden;
  border: 1px solid var(--s-light);
  box-shadow: 4px 4px 9px rgba(0, 0, 0, 0.5);
}
.image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.name {
  margin: 2rem 0rem;
  width: 100%;
}
.name h1 {
  text-align: center;
  color: #d3d9e1;
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
}
.navigations {
  width: 100%;
}
.navigations a {
  width: 100%;
  height: 40px;
  background-color: var(--s-light);
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1rem;
  border-radius: 0.3rem;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.5);
  color: var(--s-dark);
  font-size: 1.2rem;
  font-weight: 700;
  border: 1px solid transparent;
  transition: all 0.2s linear;
}
.navigations a:hover {
  background-color: var(--s-dark);
  color: var(--s-light);
  border-color: var(--s-light);
}
.navigations a:last-child {
  margin-bottom: 0rem;
}
.profile_details {
  width: 100%;
  padding: 5vw;
  background-color: #e0e0e0;
  margin-left: 350px;
}
.update_profile,
.update_password {
  width: 100%;
  margin-bottom: 5vw;
}
.delete_user h1 {
  text-shadow: -1px -1px 1px #b3b3b3, 2px 2px 1px var(--p);
}
.delete_user button {
  color: var(--p);
  background-color: #21262d;
  transition: all 0.3s linear;
}
.delete_user button:hover {
  color: #fff;
  background-color: var(--p);
  box-shadow: none;
}
.delete_user button:active {
  transform: scale(0.98);
}
