.upload_image {
  padding: 1rem;
  position: relative;
}
.upload_image label {
  padding: 0rem 1rem;
  height: 45px;
  background-color: #858585;
  color: #fff;
  font-weight: bold;
  border-radius: 0.5rem;
  cursor: pointer;
  position: relative;
  z-index: 1;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  transition: box-shadow 0.2s linear;
}
.upload_image label:hover {
  background-color: #e0e0e0;
  color: #000;
  box-shadow: 5px 5px 10px #bebebe, -5px -5px 10px #ffffff;
}
.upload_image label:active {
  box-shadow: none;
}
.upload_image input {
  position: absolute;
  top: 50%;
  left: 57px;
  transform: translate(0, -50%);
}
